import React, { Component } from 'react'
import { NavigationBar, Footer, PageHeader, LoadingScreen,SeoHeader } from 'components';
import { animateScroll } from 'react-scroll';
import { Query, withApollo } from 'react-apollo';
import { QUERIES } from 'graphql-actions';
import SimlarPosts from './SimilarPosts';
import ActiveBlog from './ActiveBlog';
import { isMobile } from 'react-device-detect';
import Helmet from 'react-helmet';

const { GET_BLOG, GET_ALL_BLOGS } = QUERIES;


const overlay = "linear-gradient(to right, rgba(0,0,0,0.5), rgba(0,0,0,0.5))"

const imageStyle = (image, layOver) => {
    const Over = layOver ? `${overlay},url(${image})` : `url(${image})`
    return { backgroundImage: Over, backgroundSize: 'cover' }

}
class Blog extends Component {

    state = {
        data: [], 
        activePost: {},
        loading:true
       
    }
    componentDidMount() {
        this.getBlog(this.props.match.params.slug)
    }


    getBlog = async (slug) => {
        this.setState({ loading: true})
        try {
            const response = await  this.props.client.query({
                query: GET_BLOG,
                variables: { slug: slug }
            })
            return this.setState({ activeData: response.data.getBlog, loading: false }, () => {
                animateScroll.scrollToTop()
            })
        } catch (error) {
            this.props.history.push("/blog")
        }
    }

    componentWillReceiveProps(props) {
        this.setState({ loading: true }, () => {
            return this.getBlog(this.props.match.params.slug)
        })
    }

  

    dataLoaded = (data) => {
        if (data._id) {
            this.setState({
                similarLoading:false
            })
            return data
        }
        return {}
    }

    render() {
        const { slug } = this.props.match.params
        const { activeData, loading } = this.state

        if (loading) return <LoadingScreen />

        return (
            <div style={{ width: '100%', overflowX: 'hidden' }}>
                <SeoHeader 
                    title={`Blog | ${activeData.title}`}
                    url={window.location.href}
                    description={activeData.blog.replace(/<[^>]*>?/gm, '').substring(0, 160)} 
                    image={activeData.blogImage}
                />
                <NavigationBar transparent />
                    <div>
                        <PageHeader
                            title=""
                            height={isMobile ? "40vh" : "60vh"}
                            image={activeData.blogImage}
                            overLay
                            containerStyle={{ backgroundPosition: 'center' }}
                        />
                        <div style={{ padding: 10, backgroundColor: '#fff',paddingTop:isMobile ? 80 :10 }}>
                            <div className="col-md-10 mx-auto" style={{ padding: 0 }}>
                                <div className="row pb-5">
                                <ActiveBlog data={activeData}/>
                                {!isMobile && <SimlarPosts activeBlog={activeData}/>}
                                </div>
                            </div>
                        </div>
                    </div>
                <Query query={GET_ALL_BLOGS}>
                    {({ data, loading, error }) => {
                        if (loading) return <div className="App">Loading</div>
                        if (error) return <div className="App">Error</div>
                        return (
                            <div className="pt-5 pb-5" style={{ backgroundColor: "#1B1B1B" }}>
                                <div className="col-sm-12 col-md-10 mx-auto">
                                    <div className="row mx-auto" style={{ marginBottom: 60 }}>
                                        <div className="col-md-6 mx-auto">
                                            <div style={{ marginBottom: 0, textAlign: 'center', textTransform: 'uppercase', fontSize: 12, fontWeight: '600', color: '#c2a54a' }}>
                                                Fresh new Reads
                                            </div>
                                            <h1 style={{ textAlign: 'center', textTransform: "capitalize", fontWeight: '600', color: '#fff' }}>
                                                Recent Posts
                                             </h1>
                                        </div>
                                    </div>
                                    <div className="row mx-auto limiter">
                                        {data.getAllBlogs.map((item, index) => {
                                                if (index > 6) return null
                                                if (slug === item.slug) return null
                                                return (
                                                    <div key={index} className="col-sm-12 col-md-4 link mb-4" onClick={() => this.props.history.push(`/blog/${item.slug}`)}>
                                                        <div style={{ height: 250, maxWidth: isMobile ? '100%' : 300, ...imageStyle(item.blogImage, true), backgroundPosition: 'center', borderRadius: 5, boxShadow: '0 8px 6px -6px #0c0c0c', padding: '20px 30px' }}>
                                                            <div style={{ display: 'flex', flexDirection: 'column', marginBottom: 10, height: '100%' }}>
                                                                <div style={{ marginTop: 'auto' }}>
                                                                    <div style={{ color: "#c2a54a", textTransform: 'uppercase', fontWeight: 'bold', fontSize: 12 }}>{item.topic}</div>
                                                                    <div style={{ color: '#fff', fontWeight: 'bold', lineHeight: '1.2em' }}>
                                                                        {item.title}
                                                                    </div>
                                                                </div>
                                                            </div>

                                                        </div>
                                                    </div>
                                                )
                                            })
                                        }
                                    </div>
                                </div>
                            </div>
                        )
                    }}
                </Query>
                <Footer />
            </div>
        )
    }
}

export default withApollo(Blog)
