import React from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { withRouter } from 'react-router-dom';
import TextTruncate from 'react-text-truncate';
const ServiceCard = ({ history, item, index }) => {
    // const isOdd = (index%2 === 0) ? {  backgroundColor: '#f1f1f1', } : { background:"linear-gradient(to left, #c2a54a, #ae9442, #74632C)",color:'#fff' }
    return (
        <div className="col-md-4 mx-auto mb-5" onClick={() => history.push(`/services/${item.name}`)}>
            <div
                className="service-card"
                style={{ backgroundColor: '#f1f1f1',height: 210, maxWidth: 300,backgroundPosition: 'center', borderRadius: 5, padding: '30px 30px' }}>
                <div>
                    <ul style={{ listStyle: 'none', display: 'flex', padding: 0 }}>
                        <li > <FontAwesomeIcon icon={item.icon} color={item.iconColor} size="2x" /></li>
                        <li style={{ marginLeft: 20 }}>   <div style={{ fontWeight: '600', fontSize: 25, lineHeight: '1em' }}>{item.title}</div></li>
                    </ul>
                </div>
                <div className="row">
                    <div className="col-md-auto" style={{ height: '50%', fontSize: 14, marginTop: 5 }}>
                        
                        <TextTruncate
                            line={4}
                            element="span"
                            truncateText=""
                            text={item.info}
                            textTruncateChild={""}
                        />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default withRouter(ServiceCard)
