import React from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faFacebookF, faTwitter, faLinkedinIn, faPinterestP, faWhatsapp, } from '@fortawesome/free-brands-svg-icons'
import { isMobile } from 'react-device-detect';

import {
    FacebookShareButton,
    TwitterShareButton,
    LinkedinShareButton,
    WhatsappShareButton,
    PinterestShareButton,
    EmailShareButton,
} from "react-share";
import { faEnvelope } from '@fortawesome/free-solid-svg-icons';
import moment from 'moment';
const ActiveBlog = ({ data}) => {
    const { title, blog, author, blogImage, createdDate } = data
    const newblog = blog.split('<img').join(`<img style="max-width:100%"`);

    const calculateWords = (blog) => {
        const wordsPerMinute = 200; // Average case.
        let textLength = blog.split(" ").length;
        if (textLength > 0) {
            let value = Math.ceil(textLength / wordsPerMinute);
            return `~ ${value} min read`
        }
        return ""
    }

    const blogDate = () => {
        const myDate = new Date(createdDate * 1)
        return moment(myDate.toUTCString()).format("LL")
    }

    return (
        <div className="col-md-8 mr-auto pt-2 pb-5" style={{ paddingRight: isMobile ? null :30 }}>
            <div>
                <h1 style={{ textTransform: 'capitalize', fontWeight: 'bold', marginTop: 10 }}>{title}</h1>
                <div style={{ color: 'rgb(194, 165, 74)', fontWeight: 'bold', marginBottom:20 }}>By {author}</div>
                <div className="col-md-12" style={{ display: 'flex', flexDirection: 'column', }}>
                    <div className="row mb-3">
                        {/* <div className="col-auto " style={{ padding: 0, display: 'flex', flexDirection: 'column' }}>
                            <div style={{ margin: 'auto 0px', color: '#ccc', fontWeight: 'bold' }}>
                                Share:
                            </div>
                        </div> */}
                        <div className="col-auto" style={{ padding:0, paddingRight:5 }}>
                            <FacebookShareButton
                                url={window.location.href}
                                quote={title}
                                className="Demo__some-network__share-button"
                            >
                                <div className="social-media-buttons">
                                    <div style={{ margin: 'auto' }}>
                                        <FontAwesomeIcon color="#ccc" icon={faFacebookF} className="link" />
                                    </div>
                                </div> 
                            </FacebookShareButton>
                        </div>
                        <div className="col-auto" style={{ padding: "0px 5px" }}>
                            <TwitterShareButton
                                url={window.location.href}
                                title={title}
                                className="Demo__some-network__share-button"
                            >
                                <div className="social-media-buttons">
                                    <div style={{ margin: 'auto' }}>
                                        <FontAwesomeIcon color="#ccc" icon={faTwitter} className="link" />
                                    </div>
                                </div>
                            </TwitterShareButton>

                        </div>
                        <div className="col-auto" style={{ padding: "0px 5px"}}>
                             <LinkedinShareButton
                                url={window.location.href}
                                title={title}
                                summary={blog.replace(/<[^>]*>?/gm, '').substring(0, 20)}
                                source={"OpenHouse Technology"}
                            >
                                <div className="social-media-buttons">
                                    <div style={{ margin: 'auto' }}>
                                        <FontAwesomeIcon color="#ccc" icon={faLinkedinIn} className="link" />
                                    </div>
                                </div>
                            </LinkedinShareButton>
                        </div>
                        <div className="col-auto" style={{ padding: "0px 5px" }}>
                            <WhatsappShareButton
                                url={window.location.href}
                            >
                                <div className="social-media-buttons">
                                    <div style={{ margin: 'auto' }}>
                                        <FontAwesomeIcon color="#ccc" icon={faWhatsapp} className="link" />
                                    </div>
                                </div>
                            </WhatsappShareButton>
                        </div>
                        <div className="col-auto" style={{ padding: "0px 5px" }}>
                          
                                <PinterestShareButton
                                    url={window.location.href}
                                    media={blogImage}
                                    description={blog.replace(/<[^>]*>?/gm, '').substring(0, 20)}
                                  
                                >
                                    <div className="social-media-buttons">
                                        <div style={{ margin: 'auto' }}>
                                            <FontAwesomeIcon color="#ccc" icon={faPinterestP} className="link" />
                                        </div>
                                    </div>
                                </PinterestShareButton>
                            
                        </div>
                        <div className="col-auto" style={{ padding: "0px 5px" }}>
                            <EmailShareButton
                                subject={`Found an interest read : ${title}`}
                                url={window.location.href}
                                className="Demo__some-network__share-button"
                            >
                                <div className="social-media-buttons">
                                    <div style={{ margin: 'auto' }}>
                                        <FontAwesomeIcon color="#ccc" icon={faEnvelope} className="link" />
                                    </div>
                                </div>
                            </EmailShareButton>
                        </div>
                    </div>
                </div>
                <div style={{ color: 'rgb(175, 175, 175)', fontSize: 14, marginBottom:10,}}>{blogDate()} {calculateWords(blog.replace(/<[^>]*>?/gm, ''))}</div>
                {isMobile && <img alt={title + "image"} src={blogImage} width="100%"/>}
            </div>
            <div onCopy={() => false} onCut={() => false} onpaste="return false" style={{ marginTop: 30, color: '#1c1c1c' }} dangerouslySetInnerHTML={{ __html:`<div oncopy="return false" oncut="return false" onpaste="return false">${newblog}</div>` }}></div>
        </div>
    )
}

export default ActiveBlog
