import React from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

const Button = ({ title,onClick,leftIcon, rightIcon, className}) => {
    return (
        <button className={`myButton ${className}`} onClick={onClick}>
            {leftIcon && <span style={{ marginRight: 15, marginTop: 2 }}>
                <FontAwesomeIcon icon={leftIcon} size="sm" />
            </span>}
           {title}
           {rightIcon && <span style={{ marginLeft: 15, marginTop: 2 }}>
                <FontAwesomeIcon icon={rightIcon} size="sm" />
            </span>}
        </button>
    )
}

export default Button
