import React, { Component } from 'react'
import { NavigationBar, Footer, PageHeader, LoadingScreen } from 'components';
import { animateScroll } from 'react-scroll';
import { withApollo } from 'react-apollo';
import { QUERIES } from 'graphql-actions';
import BlogList from './BlogList';
import ReactPaginate  from 'react-paginate';
import FilterBar from './FilterBar';
import { isMobile } from 'react-device-detect';

const { GET_ALL_BLOGS } =  QUERIES

const headerImage = "https://images.unsplash.com/photo-1534298574014-ca677bb90d44?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1789&q=80"

class Blog extends Component {

    state = {
        blogs: [],  
        filteredBlogs:[],
        data:[],
        topics: [],
        loading: true,
        offset: 0,
        elements: [],
        perPage: 6,
        currentPage: 0,
    }
    componentDidMount() {
        animateScroll.scrollToTop();
        this.recieveData()
    }

    getUnique = (arr, comp) =>{

        const unique = arr
            .map(e => e[comp])

            // store the keys of the unique objects
            .map((e, i, final) => final.indexOf(e) === i && i)

            // eliminate the dead keys & store unique objects
            .filter(e => arr[e]).map(e => arr[e]);

        return unique;
    }

    componentWillReceiveProps() {
        this.recieveData()
    }

    recieveData = async() => {
        this.setState({ loading: true });
        try {
            const res = await this.props.client.query({
                query: GET_ALL_BLOGS,
            });
            return this.setState({
                blogs: res.data.getAllBlogs,
                filteredBlogs: res.data.getAllBlogs,
                topics: this.getUnique(res.data.getAllBlogs, 'topic'),
               
            }, () => this.setElementsForCurrentPage());
        } catch (error) {
            this.setState({ loading: true });
            const message = error.message.replace("GraphQL error: ", "");
            return { message, status: false };
        }
    }


    setElementsForCurrentPage = () => {
        let elements = this.state.filteredBlogs
            .slice(this.state.offset, this.state.offset + this.state.perPage)
        this.setState({ pageCount: Math.ceil(this.state.filteredBlogs.length / this.state.perPage), }, () => {
            this.setState({
                elements: elements, loading: false 
            })
        });
    }

    handlePageClick = (data) => {
        animateScroll.scrollToTop();
        const selectedPage = data.selected;
        const offset = selectedPage * this.state.perPage;
        this.setState({ loading: true, currentPage: selectedPage, offset: offset }, () => {
            this.setElementsForCurrentPage();
        });
    }

    onTopicFilter = (topic) => {
        this.setState({ loading: true })
        if (topic === "all") {
            return this.setState({
                filteredBlogs: this.state.blogs,
                loading: false
            }, () => {
                this.setElementsForCurrentPage();
            });
        }
        const newBlogs = this.state.blogs.filter((item) => item.topic === topic)
        return this.setState({
            filteredBlogs: newBlogs,
            loading: false,
            elements: newBlogs,
            pageCount: Math.ceil(newBlogs.length / this.state.perPage)
        });
    }

    onSearch = (event) => {
        this.setState({ loading: true })
        const filted = this.state.blogs.filter((item) => item.title.toLowerCase().includes(event.target.value.toLowerCase()))
        return this.setState({
            filteredBlogs: filted,
            loading: false,
            elements: filted,
            pageCount: Math.ceil(filted.length / this.state.perPage)
        })
    }

    render() {
        let paginationElement = null;
     if (this.state.pageCount > 1) {
            paginationElement = (
                <ReactPaginate
                    previousLabel={"Prev"}
                    nextLabel={"Next"}
                    breakLabel={<div className="pagination-separator">...</div>}
                    pageCount={this.state.pageCount}
                    onPageChange={this.handlePageClick}
                    forcePage={this.state.currentPage}
                    containerClassName={"pagination"}
                    previousLinkClassName={"previous_page"}
                    nextLinkClassName={"next_page"}
                    activeClassName={"active_number"}
                    pageClassName={"inactive_number"}
                    marginPagesDisplayed={0}
                    pageRangeDisplayed={4}
                />
            );
     }
        if (this.state.loading) return <LoadingScreen />
        return (
            <div style={{width:'100%', overflowX:'hidden'}}>
                <NavigationBar/>
                <PageHeader
                    title1="B"
                    title2="log"
                    image={headerImage}
                />
                <div style={{ paddingTop: isMobile ? 60 : null }}>
                    {!isMobile && <FilterBar
                        topics={this.state.topics}
                        onTopicFilter={this.onTopicFilter}
                        onSearch={this.onSearch}
                    /> }  
                        <BlogList data={this.state.elements} onBlog={(slug) => this.props.history.push(`/blog/${slug}`)}>
                            <div className="row">
                                <div className="col-md-12">
                                    {paginationElement}
                                 </div>
                            </div>
                        </BlogList>
                        
                </div>
                <Footer />
            </div>
        )
    }
}

export default withApollo(Blog)
