import React from 'react'
import { isMobile } from 'react-device-detect';

const overlay = "linear-gradient(to right, rgba(0,0,0,0.5), rgba(0,0,0,0.5))"

const imageStyle = (image, layOver) => {
    const Over = layOver ? `${overlay},url(${image})` : `url(${image})`
    return { backgroundImage: Over, backgroundSize: 'cover', backgroundPosition: 'center' }
}


const PageHeader = ({image, overLay, title1, title2, height,containerStyle}) => {
    height= height? height : '30vh'
    if (isMobile) return null
    return (
        <div style={{...imageStyle(image, true),...containerStyle}}>
            <div style={{ height: height, display: 'flex', flexDirection: 'column' }}>
                <div className="row" style={{ margin: 'auto 0px' }}>
                    <div className="col-md-9 mx-auto">
                        {/* <div style={{ color: '#fff', fontSize: 20 }}>We Give You The Best</div> */}
                        <h1 style={{ fontSize: 50, fontWeight: 'bold', color: '#fff', margin: '0px 0px', }}>{title1}<span style={{ color: '#c2a54a' }}>{title2}</span></h1>
                        {/* <div style={{ color: '#fff', fontSize: 20 }}>in the Shortest Time</div> */}

                    </div>
                </div>
            </div>
        </div>
    )
}

export default PageHeader
