import React from 'react'
import { withRouter } from 'react-router-dom';
const menuList = [
   
     {
        name: 'E-commerce Website',
        link: "/services",
    },

     {
        name: 'Wordpress',
        link: "/services",
    },
    {
        name: 'Web Design',
        link: "/services",
    },
    {
        name: 'App Development',
        link: "/services",
    },
    
    {
        name: 'Social Media Marketing',
        link: "/services",
    },
    
    

]

const ColumnOne = ({ history }) => {

    const onNavigate = (link) => {
        window.scrollTo(0, 0);
        history.push(link)
    }

    return (
        <div className="col-md-3" style={{ color: '#e1e1e1', marginTop: 20 }}>
            <p style={{ color: '#fff', fontWeight: 'bold', textTransform:'capitalize' }}>services</p>
            {menuList.map((list, index) => ( 
                <div key={index} className="link" style={{fontSize:14, marginBottom:5}} to={list.link} onClick={() => onNavigate(list.link)}>
                    {list.name}
                </div>
            ))}
        </div>
    )
}

export default withRouter(ColumnOne)