import React from 'react'
import  TextTruncate  from 'react-text-truncate';
import { isMobile } from 'react-device-detect';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faArrowRight } from '@fortawesome/free-solid-svg-icons';
import moment from 'moment';

const BlogList = ({ data, onBlog, children }) => {
    
    const calculateWords = (blog) => {
        const wordsPerMinute = 200; // Average case.
        let textLength = blog.split(" ").length;
        if (textLength > 0) {
            let value = Math.ceil(textLength / wordsPerMinute);
            return `~ ${value} min read`
        }
        return ""
    }
    const blogDate = (createdDate) => {
        const myDate = new Date(createdDate * 1)
        return moment(myDate.toUTCString()).format("LL")
    }

        return (
            <div>
                <div style={{ backgroundColor: '#1B1B1B' }}>
                    <div className="row mx-auto">
                        <div className="col-md-9 mx-auto pt-5 pb-5">
                            {data.map((item, index) => {
                                const { _id, slug, title, topic, blog, blogImage, author, createdDate} = item
                                if (isMobile) {
                                    return (
                                        <div class="card mb-3" style={{ width: '100%', }} onClick={() => onBlog(slug)}>
                                            <img src={blogImage} class="card-img-top" alt="..."/>
                                                <div class="card-body">
                                                <div className="row mb-2">
                                                    <div className="col-6">
                                                        <div style={{ color: 'rgb(175, 175, 175)', fontWeight: 'bold', fontSize: 14, textTransform: '' }}>By {author}</div>
                                                    </div>
                                                    <div className="col-6 text-right">
                                                        <div style={{ textTransform: 'uppercase', color: 'rgb(175, 175, 175)', fontWeight: 'bold', fontSize: 14 }}>#{topic}</div>
                                                    </div>
                                               </div>
                                                <h5 class="card-title" style={{ fontWeight: 'bold', marginBottom:5 }}> {title}</h5>
                                                <div style={{ marginBottom: 10, color: 'rgb(175, 175, 175)', fontSize: 14, textTransform: '' }}>{blogDate(createdDate)} {calculateWords(blog.replace(/<[^>]*>?/gm, ''))}</div>
                                                <TextTruncate
                                                    line={2}
                                                    element="div"
                                                    truncateText=""
                                                    text={blog.replace(/<[^>]*>?/gm, '')}
                                                    textTruncateChild={""}
                                                />
                                                <hr/>
                                                <div className="row mb-1" onClick={() => onBlog(slug)}>
                                                        <div className="col-6">
                                                        <div style={{ fontSize: 18, textTransform: '' }} onClick={() => onBlog(slug)}>Read Article</div>
                                                        </div>
                                                        <div className="col-6 text-right">
                                                        <div onClick={() => onBlog(slug)}>
                                                                <FontAwesomeIcon icon={faArrowRight} />
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                        </div>
                                    )
                                }
                                return (
                                    <div key={_id} className="row limiter mx-auto" style={{ height: 250, backgroundColor: '#fff', borderRadius: 5, marginBottom: 20, cursor: 'pointer' }} onClick={() => onBlog(slug)}>
                                        <div className="col-md-4" style={{ backgroundImage: `url(${blogImage})`, backgroundSize: 'cover', backgroundPosition: 'center', borderTopLeftRadius: 5, borderBottomLeftRadius: 5 }}></div>
                                        <div className="col-md-8 mx-auto" style={{ flexDirection: 'column', display: 'flex', padding: 20 }}>
                                            <div style={{ margin: 'auto' }}>
                                                <div style={{ marginBottom: 10, textTransform: 'uppercase', color: 'rgb(175, 175, 175)', fontWeight: 'bold', fontSize: 14 }}>Topic: <span style={{color:'#c2a54a'}}>{topic}</span></div>
                                                <h4 style={{ fontWeight: 'bold' }}>
                                                    {title}
                                                </h4>
                                                <div style={{ marginBottom: 10, color: 'rgb(175, 175, 175)', fontSize: 14, textTransform: '' }}>{blogDate(createdDate)} {calculateWords(blog.replace(/<[^>]*>?/gm, ''))}</div>
                                                <TextTruncate
                                                    style={{ marginBottom: 20 }}
                                                    line={3}
                                                    element="span"
                                                    truncateText=""
                                                    text={blog.replace(/<[^>]*>?/gm, '')}
                                                    textTruncateChild={""}
                                                />
                                                <div style={{ color: 'rgb(175, 175, 175)', fontWeight: 'bold', fontSize: 14, textTransform: '' }}>By {author} </div>
                                                {/* <div style={{ color: 'rgb(175, 175, 175)', fontWeight: 'bold', fontSize: 10, textTransform: '' }}>/div> */}
                                            </div>
                                        </div>
                                    </div>
                                )
                            })}
                        </div>
                    </div>
                    {children}
                </div>
            </div>
        )
    }

export default BlogList