import React from "react";
import { Route, BrowserRouter as Router, Switch } from "react-router-dom";
import { Home, BlogPages, Projects, ServicePages, Contact, Qoutation} from 'pages';


export default (
    <Router>
        <Switch>
            <Route path="/" exact component={Home} />
            <Route path="/blog" exact component={BlogPages.Blogs} />
            <Route path="/blog/:slug" exact component={BlogPages.SingleBlog} />
            <Route path="/projects" exact component={Projects} />
            <Route path="/services" exact component={ServicePages.Services} />
            <Route path="/services/:service" exact component={ServicePages.SingleService} />
            <Route path="/contact" exact component={Contact} />
            <Route path="/qoute" exact component={Qoutation} />
            <Route component={Home} />
        </Switch>
    </Router>
);
