import React from 'react'
import { withRouter } from 'react-router-dom';
import { animateScroll } from 'react-scroll';
import { Query } from 'react-apollo';
import { GET_ALL_BLOGS } from 'graphql-actions/queries';
import Moment from 'react-moment';
import TextTruncate from 'react-text-truncate';

const imageStyle = (image) => {
    const bg =  `url(${image})`
    return { backgroundImage: bg, backgroundSize: 'cover' }
    
}


const Column4 = ({ history }) => {

    const onNavigate = (link) => {
        animateScroll.scrollToTop();
        history.push('/blog/' + link)
    }

    return (
        <div className="col-md-3 col-sm-12" style={{ color: '#484848', marginTop: 20 }}>
            <p style={{ color: '#fff', fontWeight: 'bold' }}>Recent News</p>
            <Query query={GET_ALL_BLOGS}>
                {({ data, loading, error }) => {
                    if (loading) return <div className="App">Loading</div>
                    if (error) return <div className="App">Error</div>
                    return data.getAllBlogs.map((news, index) => {
                        if (index > 1) return null
                        return (
                            <div key={index} className="row max-auto recent-news" style={{ marginBottom: 10 }} onClick={() => onNavigate(news.slug)}>
                                <div className="col-auto recent-news-image">
                                    <div className="col-md-12 " style={{ width: 50, height: 50, ...imageStyle(news.blogImage), backgroundPosition: 'center', borderRadius: 5 }} />
                                </div>
                                <div className="col-7 link" style={{ paddingLeft: 0, }}>
                                        <TextTruncate
                                            className="recent-news-info"
                                            style={{ fontSize: 10, marginBottom: 5, fontWeight: 'bold', color: '#fff', textTransform: 'capitalize' }}
                                            line={1}
                                            element="div"
                                            text={news.title}
                                        />
                                    <div className="recent-news-info" style={{ fontSize: 10, color: '#fff'}}>
                                        <Moment format="MMMM D YYYY" withTitle>
                                            {
                                                new Date(1 * news.createdDate)}
                                        </Moment>
                                    </div>
                                    <div className="recent-news-info" style={{ fontSize: 10, marginBottom: 5, fontWeight: 'bold', color: '#fff', textTransform: 'capitalize'}}>
                                        {news.topic}
                                    </div>

                                    
                                </div>
                            </div>

                        )
                    })
                }}
            </Query>
        </div>
    )
}

export default withRouter(Column4)