import React, { useEffect, useState } from 'react'
import Column1 from './Column1'
import Copyright from './Copyright';
import FooterHeader from './FooterHeader';
import Column2 from './Column2';
import Column3 from './Column3';
import Column4 from './Column4';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faArrowUp } from '@fortawesome/free-solid-svg-icons';
import { animateScroll } from "react-scroll"

const overlay = "linear-gradient(to right, rgba(0,0,0,0.5), rgba(0,0,0,0.5))"
const bird = "https://images.pexels.com/photos/997443/pexels-photo-997443.jpeg?auto=compress&cs=tinysrgb&dpr=3&h=750&w=1260"
const imageStyle = (image, layOver) => {

    const Over =  `${overlay}, url(${image})`
    return { backgroundImage: Over, backgroundSize: 'cover' }

}



const Footer = () => {
    const [isTop, setIsTop] = useState(true)

    const handleScroll = () => {
        if (window.scrollY < 100) {
           setIsTop(true)
        } else {
            setIsTop(false)
        }
    };

    useEffect(() => {
        window.addEventListener("scroll", handleScroll);
    }, []);
    
    
    return (
        <div>
        <section style={{ minHeight: '70vh', ...imageStyle(bird), paddingTop: 100, paddingBottom:30}}>
            <div className="col-md-9  mx-auto">
                    <FooterHeader/>
                <div className="row limiter mx-auto" style={{ marginTop: 100 }}>
                    <Column1 />
                    <Column2/>
                    <Column3 />
                    <Column4 />
                </div>
            </div>
            </section>
            <Copyright />
            <div className="scroller-to-top" onClick={() => animateScroll.scrollToTop()} style={{ bottom: isTop? -100 :30 }}>
                    <div style={{ margin: 'auto' }}>
                        <FontAwesomeIcon icon={faArrowUp} color="#fff" />
                    </div>
            </div>
        </div>
    )
}

export default Footer
