import React, {useState} from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faShoppingBag, faSearch, faArrowRight, faBars, faTimes } from '@fortawesome/free-solid-svg-icons'
import { withRouter, Link } from "react-router-dom";
import { Button } from 'components';
import { isMobile } from 'react-device-detect';
import { scroller } from 'react-scroll';
const links = [
    {
        name: "Home",
        to: "/"
    },
    {
        name: "Company",
        to: "/"
    },
    {
        name: "Services",
        to: "/services"
    },
    {
        name: "Projects",
        to: "/projects"
    },
    {
        name: "Blog",
        to: "/blog"
    },
    {
        name: "Contact",
        to: "/contact"
    },
]


const NavigationBar = ({ match, history, location, transparent}) => {
    
    const [menu, setMenu] = useState(1000);
    
    const toggleMenu = (value) => {
        setMenu(value)
    }

   const onNavLink = (link) => {
       if (link === "Company") {
            return scroller.scrollTo("company", {
                duration: 500,
                hashSpy: true,
                delay: 100,
                smooth: true,
                offset: 0
            });
        }
       return null
    }


    if (isMobile) {
        return (
            <div>
                <div style={{ position: 'fixed', left: menu, height: '100vh', zIndex: 2, transition: '0.3s', backgroundColor: '#1e1e1e', width:'100%' }}>
                    <div style={{ position: 'absolute', top: 20, right: 40, left:30}}>
                        <div className="row">
                            {/* <div
                                onClick={() => history.push("/")}
                                style={{ fontWeight: 'bold', fontSize: 20, color: '#fff', margin: 'auto 0px', cursor: 'pointer', }}>
                                OpenHouse <span style={{ color: '#C2A54A' }}>Tech</span>
                            </div> */}
                            <div style={{ fontWeight: 'bold', color: '#fff', margin: 'auto 0px auto auto', cursor: 'pointer', }}>
                                <FontAwesomeIcon icon={faTimes} size="2x" color={'#fff'} onClick={() => toggleMenu(1000)} />
                            </div>
                        </div>
                    </div>
                    <div style={{marginTop:75, backgroundColor:'#1e1e1e', height:'100%', paddingTop:30, paddingLeft:30, color:'red', fontSize:20}}>
                        {links.map((item) => (
                          
                            <div style={{ marginBottom: 10}}>
                                <Link
                                    to={item.to}
                                    style={{ color: '#fff', fontSize: 25 }}
                                    className="navigation-links"
                                    onClick={() => toggleMenu(1000)}
                                > {item.name}</Link>
                            </div>
                        ))}
                    </div>
                </div>
                <div style={{ backgroundColor: '#000', position: 'fixed', padding: "20px 30px 20px 40px", width: '100%', zIndex: 1, display:'flex', flexDirection:'column' }}>
                    <div className="row">
                        <div
                            onClick={() => history.push("/")}
                            style={{ fontWeight: 'bold',fontSize:20, color: '#fff', margin: 'auto 0px', cursor: 'pointer', }}>
                            OpenHouse <span style={{ color: '#C2A54A' }}>Tech</span>
                        </div>
                        <div style={{ fontWeight: 'bold', color: '#fff', margin: 'auto 0px auto auto', cursor: 'pointer', }}>
                            <FontAwesomeIcon icon={faBars} size="2x" color={'#fff'} onClick={() => toggleMenu(0)} />
                        </div> 
                    </div>
               </div>
            </div>
        )
    }
    return (
        <div class="" style={{ backgroundColor: transparent ? "transparent" :'#000', paddingTop: 10 , paddingBottom:10,position:transparent? "absolute" : "unset", width:'100%' }}>
            <div className="container-fluid" style={{maxWidth:1440}}>
            <div className="row mx-auto" style={{ height: 50,}}>
            <div className="col-md-3 mx-auto" style={{ display: 'flex', flexDirection: 'column', }}>
                <div 
                    onClick={() =>  history.push("/")}
                    style={{fontWeight:'bold', color:'#fff', margin:'auto 0px', cursor:'pointer'}}>
                    OpenHouse <span style={{ color: '#C2A54A' }}>Tech</span>
                </div> 
            </div>
            <div className="col-md-6" style={{ display: 'flex', flexDirection: 'column', }}>
                <ul style={{ listStyle: 'none', margin: 'auto', textAlign: 'center' }}>
                    {links.map((data, index) => {
                        const isHome = match.path === "/" && data.name.toLowerCase() === "home";
                        const isActive = isHome || match.path.includes(data.name.toLowerCase());

                        return (
                        <li key={index} style={{ display: 'inline-block', margin: 'auto 10px', color: '#fff', fontSize: 14, fontWeight: 'bold' }}>
                            {<span style={{ marginRight: '5px' }}>
                                    <FontAwesomeIcon icon={faArrowRight} size="sm" color={isActive ? '#fff' : "transparent"}/>
                            </span>
                            }
                            <Link
                                    to={data.to}
                                    className="navigation-links"
                                    onClick={()=> onNavLink(data.name)}
                            > {data.name}</Link>
                        </li>
                        )}
                    )}
                </ul>
            </div>
            <div className="col-md-3" style={{ display: 'flex', flexDirection: 'column', }}>
                <ul style={{ listStyle: 'none', margin: 'auto', textAlign: 'center' }}>
                    <li style={{ display: 'inline-block', margin: 'auto', color: '#fff' }}>
                        <FontAwesomeIcon icon={faShoppingBag} size="sm" />
                    </li>
                    <li style={{ display: 'inline-block', margin: 'auto 15px', color: '#fff' }}>
                        <FontAwesomeIcon icon={faSearch} size="sm" />
                    </li>
                    <li style={{ display: 'inline-block', margin: 'auto 12px', color: '#fff' }}>
                        <Button
                            className="outline"
                            title="Free website Quotation"
                            onClick={() =>  history.push("/qoute")}
                        />
                    </li>
                </ul>
            </div>
            </div>
            </div>
        </div>
    )
}

export default withRouter(NavigationBar)
