import React from 'react'
import { Query } from 'react-apollo';
import { QUERIES } from 'graphql-actions';
import { withRouter } from 'react-router-dom';

const { GET_SIMILAR_BLOGS } = QUERIES;
const SimlarPosts = ({ activeBlog, history }) => {
    const input = {
        id: activeBlog._id,
        topic: activeBlog.topic
    }
    return (
        <Query query={GET_SIMILAR_BLOGS} variables={{input:input}}>
            {({ data, loading, error }) => {
                if (loading) return <div className="App"></div>
                if (error) return <div className="App"></div>
                if(data.getSimilarBlogs.length < 1) return null
                return (
                        <div className="col-md-4 ml-auto" style={{ paddingTop: 60, miHeight: 100, position: 'relative', backgroundColor: '#fff' }}>
                            <div style={{ border: '1px solid #c1c1c1', maxWidth: 300, borderRadius: 5 }}>
                                <div style={{ padding: "10px 20px", backgroundColor: '#F1F1F1', borderBottom: '1px solid #c1c1c1', borderTopRightRadius: 3, borderTopLeftRadius: 3 }}> Similar Posts</div>
                                {data.getSimilarBlogs.map((item, index) => {
                                    const { title, topic, slug } = item
                                    if (index > 5) return null
                                    const last = index === 5 ? null : '1px dotted #c1c1c1'
                                        return (
                                            <div
                                                key={index}
                                                onClick={() => history.push(`/blog/${slug}`)}
                                                className="link" style={{ padding: "10px 20px", color: '#1c1c1c', fontWeight: 'bold', borderBottom: last, margin: 'auto' }}>
                                                <div style={{ color: "#c2a54a", textTransform: 'uppercase', fontSize: 12 }}>{topic}</div>
                                                <div style={{ textTransform: 'capitalize', }}>{title}</div>
                                            </div>
                                        )
                                })}
                            </div>
                        </div>
                    )
                }
            } 
        </Query>
    )
}

export default withRouter(SimlarPosts);