import React, { Component } from 'react'
import { NavigationBar, Footer, PageHeader } from 'components';
import { animateScroll } from 'react-scroll';

import Marketing from './Marketing';
import Mobile from './Mobile';
import WebServices from './WebServices';
import { serviceList } from 'dummy-data';
import { isMobile } from 'react-device-detect';

const image1 = "https://images.unsplash.com/photo-1485470733090-0aae1788d5af?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=791&q=80"

class Services extends Component {

    state = {
        loading:true

    }
    componentDidMount() {
        animateScroll.scrollToTop();
        this.setState({
            loading: false,
        })
    }

    componentWillReceiveProps() {
        animateScroll.scrollToTop();
       
    }

    filterData = (type) => serviceList.filter((i) => i.type === type)
    


    render() {

        return (
            <div >
                <NavigationBar />
                <PageHeader
                    title1="IT"
                    title2=" Services"
                    image={image1}
                    // overLay
                />
                <div style={{ paddingTop:isMobile ? 60 : null }}>
                    <WebServices data={this.filterData('web')} />
                    <Mobile data={this.filterData('mobile')} />
                    <Marketing data={this.filterData('marketing')} />
                </div>
                <Footer />
            </div>
        )
    }
}

export default Services
