import React from 'react'
import ServiceCard from '../serviceCard'

export default function WebServices({ data}) {
    return (
        <div className="pt-5 pb-5" style={{ backgroundColor: "#1B1B1B", margin:'auto' }}>
            <div className="col-md-10 mx-auto limiter">
                <div className="row mx-auto"  style={{ marginBottom: 60 }}>
                    <div className="col-md-6 mx-auto">
                        <div style={{ marginBottom: 0, textAlign: 'center', textTransform: 'uppercase', fontSize: 12, fontWeight: '600', color: '#c2a54a' }}>
                            You looking for a stunning website?
                                </div>
                        <h1 style={{ textAlign: 'center', textTransform: "capitalize", fontWeight: '600', color: '#fff' }}>
                           Website Services
                                </h1>
                    </div>
                </div>
                <div className="row mx-auto">
                    {data.map((item, index) => {
                        if (index > 5) return null
                        index = index > 9 ? index - 5 : index
                        return (
                            <ServiceCard
                                key={index}
                                index={index}
                                item={item}
                            />
                        )
                    })}
                </div>
            </div>

        </div>
    )
}