import React, { Component } from 'react'
import { NavigationBar, PageHeader, Footer } from 'components';
import { serviceList } from 'dummy-data';

const image1 = "https://images.unsplash.com/photo-1485470733090-0aae1788d5af?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=791&q=80"
class SingleService extends Component {

    constructor(props) {
      super(props)
      this.state = {
          activeService: {}
      };
    };
    
    componentDidMount() {
        const service = serviceList.find((i) => i.name === this.props.match.params.service);
        if (!service) return this.props.history.goBack()
        return this.setState({
            activeService: service
        })
    }

    render() {
        const { activeService } = this.state
        return (
            <div>
                <NavigationBar />
                <PageHeader
                    title1={activeService.titleOne}
                    title2={activeService.titleTwo}
                    image={image1}
                // overLay
                />
                <Footer />
            </div>
        )
    }
}

export default SingleService;
