import React from 'react'
import { Button } from 'components';
import { faArrowRight } from '@fortawesome/free-solid-svg-icons'
import { scroller } from "react-scroll";
import { withRouter } from 'react-router-dom';
import { isMobile } from 'react-device-detect';
// const image1 = "https://ohtech.co.za/assets/images/backgrounds/bg-capetown.jpg"
const image1 = "https://images.unsplash.com/photo-1510519138101-570d1dca3d66?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1462&q=80"
const overlay = "linear-gradient(to right, rgba(0,0,0,0.7), rgba(0,0,0,0.7))"
const imageStyle = (image, layOver) => {
    const Over = layOver ? `${overlay},url(${image})` : `url(${image})`
    return { backgroundImage: Over, backgroundSize: 'cover', backgroundPosition: isMobile ?"-30vh center":'center -40vh'}
}
const onLearnMore = () => {
    scroller.scrollTo("services", {
        duration: 500,
        hashSpy: true,
        delay: 100,
        smooth: true,
        offset: 0
    });
}
const LandingSection = ({history}) => {
    return (
        <div style={{ height: '83vh', ...imageStyle(image1), paddingTop: isMobile ? 60 : null }}>
            <div style={{ height: '90vh', display: 'flex', flexDirection: 'column' }}>
                <div className="row" style={{ margin: 'auto 0px'}}>
                    <div className="col-md-9  mx-auto" style={{marginTop:-90}}>
                        <div style={{ color: '#fff', fontSize: 20 }}>We Give You The Best</div>
                        <h1 style={{ fontSize: 100, fontWeight: 'bold', color: '#fff', margin: '0px 0px', }}>IT <span style={{ color: '#c2a54a' }}>Product</span></h1>
                        <div style={{ color: '#fff', fontSize: 20 }}>in the Shortest Time</div>
                        <div style={{ marginTop: 30 }}>
                            <ul style={{ listStyle: 'none', padding: 0 }}>
                                <li style={{ display: 'inline-block', marginRight: 20 }}>
                                    <Button className="primary" title="Learn More" rightIcon={faArrowRight} onClick={onLearnMore}/>
                                </li>
                                <li style={{ display: 'inline-block' }}>
                                    <Button
                                        className="outline" title="Get in Touch"
                                        rightIcon={faArrowRight}
                                        onClick={() => history.push('/contact')}
                                    />
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
export default withRouter(LandingSection)