import React from 'react'

function FilterBar({ topics, onTopicFilter, onSearch}) {
    return (
        <div style={{ padding: 10, backgroundColor: '#000' }}>
            <div className="col-md-9 mx-auto">
                <div className="row">
                    <div className="col-md-7" style={{ display: 'flex', flexDirection: 'column' }}>
                        <ul style={{ listStyle: 'none', margin: 'auto 0px', paddingLeft: 0 }}>
                            <li style={{ display: 'inline-block', margin: 'auto 10px', color: '#fff', fontSize: 14, fontWeight: 'bold', textTransform: 'capitalize' }}>
                                <div onClick={() => onTopicFilter('all')} className="link">All</div>
                            </li>
                            {topics.map((data, index) => {
                                if (index > 8) return null
                                return (
                                    <li key={index} style={{ display: 'inline-block', margin: 'auto 10px', color: '#fff', fontSize: 14, fontWeight: 'bold', textTransform: 'capitalize' }}>
                                        <div onClick={() => onTopicFilter(data.topic)} className="link">{data.topic}</div>
                                    </li>
                                )
                            }
                            )}
                        </ul>
                    </div>
                    <div className="col-md-5 ml-auto" >
                        <input className="input white" placeholder="Search Title" style={{ margin: 'auto', minWidth: '100%' }} onChange={onSearch} />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default FilterBar
