import React from 'react'
import { Helmet } from 'react-helmet';

function SeoHeader({title, url,description, image}) {
    return (
        <Helmet>
                <title>{`OpenHouse Tech | ${title}`}</title>
                <meta name="title" content={`OpenHouse Tech | ${title}`} />
                <meta name="description" content={description} />
                <meta name="subject" content="We are a boutique digital tranformation constultancy and development company"/>
                <meta name="copyright"content="OpenHouse Technology"/>
                <meta name="keywords" content="your, tags"/>
                
                
                <meta property="og:type" content="website" />
                <meta property="og:url" content={url} />
                <meta property="og:title" content={`OpenHouse Tech | ${title}`} />
                <meta property="og:description" content={description} />
                <meta property="og:image" content={image} />


                <meta property="twitter:card" content="summary_large_image" />
                <meta property="twitter:url" content={url} />
                <meta property="twitter:title" content={`OpenHouse Tech | ${title}`} />
                <meta property="twitter:description" content={description} />
                <meta property="twitter:image" content={image} />
        </Helmet>
                                                    
        )
 }
                                                
 export default SeoHeader
